@import "src/common/styles/sass/variables";
@import "src/common/styles/sass/mixins";

.project {
    width: 450px;
    display: flex;
    flex-direction: column; //???????
    text-align: center;
    position: relative;
    padding: 20px;

    .projectInfo {
        padding: 25px 20px;
        position: absolute;
        bottom: -220px;
        width: 100%;
        z-index: 1;
        visibility: hidden;

        .projectTitle {
            @include text($fontFamily_1, 30px, 1.4074, 500);
            margin-bottom: 19px;

        }

        .projectDescription {
            @include text($fontFamily_1, 17px, 30px, 400);
            color: $descriptionTextColor; //?????

        }

    }

    &:hover {
        &::before {
            position: absolute;
            content: "";
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            width: 100%;
            height: 100%;
            //z-index: 5;
            background-image: linear-gradient(90deg, #36d430, #09d81e);
            border-radius: 5px;
            opacity: 0.85;
            transition: all .5s;

        }

        &:hover .projectInfo {
            bottom: 0;
            visibility: visible;
            transition: all .5s;
            //z-index: 5;
            background: -webkit-linear-gradient(top, transparent, $bgColor_2 80%);

        }
    }


    .image {
        @include bgSettings();
        @include flex(row, center, center, nowrap);
        height: 350px;
        border-radius: 5px;

    }



}











